<template>
  <div id="more-search-list">
    <header>
      <img @click="gohome" src="../../assets/imgs/images/pre.png" />
      <form action="/">
        <van-search
          v-model="inputvalue"
          show-action
          placeholder="请输入搜索关键词"
        >
          <template #action>
            <div @click="startSearch">搜索</div>
          </template>
        </van-search>
      </form>
    </header>
    <div class="section">
      <div class="searhc-item" v-show="type !== 7">
        <SearchItem
          v-for="item in searchMoreThemeList"
          class="search-item-com"
          :key="item.id"
          :id="item.id"
          :pic="item.Image"
          :title="item.Title"
          :type="type"
          :SecondTitle="item.SecondTitle"
          :isVipFree="item.isVipFree ?? false"
          :freeType="item.freeType ?? false"
          :value="inputvalue"
        ></SearchItem>
      </div>
      <div class="searhc-item-poem" v-show="type == 7">
        <PoemBook
          class="search-item-book"
          v-for="(item, i) in searchMoreThemeList"
          :key="item.id"
          :id="item.id"
          :itemindex="i + 1"
          :title="item.Title"
          :author="item.author_name"
          :SecondTitle="item.SecondTitle"
          :type="type"
          :isVipFree="item.isVipFree ?? false"
          :freeType="item.freeType ?? false"
          :value="inputvalue"
        ></PoemBook>
      </div>
    </div>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
import PoemBook from "@/components/PoemBook.vue";
export default {
  name: "MoreSearchList",
  data() {
    return {
      inputvalue: this.$route.params.value!==undefined?this.$route.params.value:JSON.parse(localStorage.getItem("clickItemData")).value,
      type: this.$route.params.type!==undefined?this.$route.params.type:JSON.parse(localStorage.getItem("clickItemData")).type,
      searchMoreThemeList: [],
    };
  },
  components: { SearchItem, PoemBook },
  created() {
   this.$nextTick(()=>{
     this.getmoreList();
   })
  },
  methods: {
    gohome() {
      this.$router.back(-1);
    },
    async getmoreList() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/Search",
        data: {
          type: this.type,
          str: this.inputvalue,
          page: 1,
          limit: 20,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.searchMoreThemeList = data.data.data;

    },
    startSearch() {
      this.$router.push({
        path: "/search/search-section",
        query: {
          value: this.inputvalue,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#more-search-list {
  header {
    z-index: 999;
    position: sticky;
    top: 0;
    left: 0;
    background-color: #fff;
    padding: 0px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    img {
      width: 10px;
      margin-right: 10px;
    }
    form {
      flex: 1;
      font-size: 20px;
    }
  }
  .section {
    padding-bottom: 35px;
    .searhc-item-poem {
      // margin: 0 20px;
      padding-top: 20px;
      border-top: 1px solid var(--theme-title-bottom);
    }
  }
}
</style>